body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

#root{
  display: flex;
  min-height: 100vh;
  flex-direction: column;
}

html {
  box-sizing: border-box
}

*,::after,::before {
  box-sizing: inherit
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: white;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

:root {
  --primary: #377DFF;
  --primary-light: #DFEEFF;
  --primary-dark:#334681  ;
  --info: #559DF3;
  --info-light: #77B1F5;
  --danger: #EE6352;
  --danger-light: #F18275;
  --dark: #334681;
  --dark-light: #495A69;
  --dark-transparent: rgba(0, 0, 0, 0.3);
  --grey: #DCE0D9;
  --grey-light: #F2F2F2;
  --grey-transparent: rgba(0, 0, 0, .02);
  --white: #FFFEFC;
  --white-light: #FFFEFD;
  --font-sans-serif: 'Fira Sans Condensed', sans-serif;
  --font-serif: 'Montserrat', serif;
  --font-monospace: 'Menlo', monospace;
  --border-radius: 5px;

  --spacing: 25px;
  --max-width: 1000px;
  --max-content-width: 800px;

  --break-desktop: 800px;
}

body.dark {
  --dark: #FFFEFC;
  --dark-light: #FFFEFD;
  --white: #1C3144;
  --white-light: #495A69;
}

