.logo {
  min-width: 20px;
  min-height: 25px;
  display: inline-block;
  padding: 20px 0;
}

.logo svg {
  -webkit-transform: rotate(45deg) translateY(-10px);
          transform: rotate(45deg) translateY(-10px);
  -webkit-transform-origin: center;
          transform-origin: center;
  vertical-align: middle;
}

.logo span {
  margin: 0px calc(var(--spacing) / 2);
  font-weight: 500;
}

.logo .primary { fill: var(--primary); }
.logo .primary-light { fill: var(--primary-light); }
.logo .info { fill: var(--info); }
.logo .info-light { fill: var(--info-light); }
